import React from 'react';
import ReactDOM from 'react-dom';
import './style/index.css';
//URL
let imagesDate = require('./imagesDate.json');

imagesDate = (function genImageUrl(imagesDateArr) {
    for (let i = 0,j=imagesDateArr.length; i < j ;  i++) {
        const singleImageDate = imagesDateArr[i];
        singleImageDate.imageUrl = require('./imgaes/'+singleImageDate.fileName);
        imagesDateArr[i] = singleImageDate; 
    }
    return imagesDateArr;    
})(imagesDate);

//
function getRangeRandom(low,high) {
    return Math.ceil(Math.random() * (high-low) + low);    
}

//30
function get30DegRandom() {
    return ((Math.random() > 0.5?'' : '-')+Math.ceil(Math.random() * 30));
}

//
function cloneObj(obj){
    var newObj = {};
    if (obj instanceof Array) {
        newObj = [];
    }
    for (var key in obj) {
        var val = obj[key];
        // newObj[key] = typeof val === 'object' ? arguments.callee(val) : val;
        // //arguments.callee , 
        newObj[key] = typeof val === 'object'
            ? cloneObj(val)
            : val;
    }
    return newObj;
}

class ImgFigure extends React.Component{
    constructor(props){
        super(props);
        this.handleClick =this.handleClick.bind(this);
    }
    /*
     * imgFigure
     */ 
    handleClick(e){
        if(this.props.arrange.isCenter){
            this.props.inverse();
        }else{
            this.props.center();
        }
        e.stopPropagation();
        e.preventDefault();
    }
    
    render(){
        /*
         * 1   style = this.props.arrange.posstyleObjthis.props.arrange.pos
         *     styleObjhandleClicksetstate
         *     reactMozTransform
         *     
         */    

        let styleObj = {};

        //props
        if(this.props.arrange.pos){
            styleObj = cloneObj(this.props.arrange.pos);   //fix 
        }
        //0   
        if(this.props.arrange.rotate){
            (['MozTransform', 'msTransform', 'WebkitTransform', 'transform']).forEach(value => {
                styleObj[value] = 'rotate(' + this.props.arrange.rotate + 'deg)';
            });
        }

        if(this.props.arrange.isCenter){
            styleObj.zIndex = 11;
        }

        let imgFigureClassName = 'img-figure';
        imgFigureClassName += this.props.arrange.isInverse? ' is-inverse':'';

        return (
            <div>
                <figure className={imgFigureClassName} style={styleObj} onClick={this.handleClick}>
                    <img src={this.props.date.imageUrl} alt={this.props.date.title}/>
                    <figcaption>
                        <h2 className="img-title">{this.props.date.title}</h2>
                        <div className="img-back" onClick={this.handleClick}>
                            <p>
                                {this.props.date.desc}
                            </p>   
                        </div>
                    </figcaption>
                </figure>
            </div>
        );
    }
}

class ControllerUnit extends React.Component {
    constructor(props) {
        super(props);
        this.handleClick = this
            .handleClick
            .bind(this);
    }

    handleClick(e) {
        //
        if(this.props.arrange.isCenter){
            this.props.inverse();
        }else{
            this.props.center();
        }
        e.preventDefault();
        e.stopPropagation();   
    }

    render() {
        let controllerUnitClassName = 'controller-unit';
        // 
        if(this.props.arrange.isCenter){
            controllerUnitClassName += ' is-center';
            // 
            if(this.props.arrange.isInverse){
                controllerUnitClassName += ' is-inverse';
            }
        }
        return (
            <span className={controllerUnitClassName} onClick={this.handleClick}></span>
        );
    }
}


class ReactGallery extends React.Component{
    constructor(props){
        super(props);
        this.Constant={
            centerPos:{
                left:0,
                right:0
            },
            hPosRange : {  //X
                leftSecX:[0,0],
                rightSecX:[0,0],
                y:[0,0]
            },
            vPosRange : {  //y
                x:[0,0],
                topY:[0,0]
            }

        };
        this.state = {
            imgsArrangeArr: [
                // {
                //     pos : {
                //         left : '0',
                //         top : '0'
                //     },
                //     rotate : 0,  //
                //     isInverse : false  //
                //     isCenter : false    //
                // }
            ]
        };
    }
    /*
     * 
     * @param index inverseindex
     * @return {Function} 
     */
    inverse(index){
        return () =>{
            let imgsArrangeArr = this.state.imgsArrangeArr;
            imgsArrangeArr[index].isInverse = !imgsArrangeArr[index].isInverse;

            this.setState({
                imgsArrangeArr :imgsArrangeArr
            });
        };
    }


    /*
     * 
     * @param centerIndex 
     */ 
    rearrange(centerIndex){
        let imgsArrangeArr = this.state.imgsArrangeArr,
            Constant = this.Constant,
            centerPos = Constant.centerPos,
            hPosRange = Constant.hPosRange,
            vPosRange = Constant.vPosRange,
            hPosRangeLeftSecX = hPosRange.leftSecX,
            hPosRangeRightSecX = hPosRange.rightSecX,
            hPosRangeY = hPosRange.y,
            vPosRangeTopY = vPosRange.topY,
            vPosRangeX = vPosRange.x,
            
            imgsArrangeTopArr =[],
            topImgNum = Math.floor(Math.random() * 2),  //
            topImgSpliceIndex = 0,
            
            imgsArrangeCenterArr = imgsArrangeArr.splice(centerIndex,1);

        // centerIndex , centerIndex 
        imgsArrangeCenterArr[0]={
            pos:centerPos,
            rotate:0,
            isCenter:true
        }; 
        
        //
        topImgSpliceIndex = Math.ceil(Math.random() * (imgsArrangeArr.length - topImgNum));
        imgsArrangeTopArr = imgsArrangeArr.splice(topImgSpliceIndex,topImgNum);

        //
        imgsArrangeTopArr.forEach((value,index) => 
            imgsArrangeTopArr[index] = {
                pos : {
                    top: getRangeRandom(vPosRangeTopY[0],vPosRangeTopY[1]),
                    left: getRangeRandom(vPosRangeX[0],vPosRangeX[1])
                },
                rotate : get30DegRandom(),
                isCenter:false
            } 
        );

        //
        for (let i = 0, j = imgsArrangeArr.length, k = j / 2; i < j; i++) {
            let hPosRangeLORX = null;
            
            //
            if(i < k){
                hPosRangeLORX = hPosRangeLeftSecX;
            }else{
                hPosRangeLORX = hPosRangeRightSecX;
            }
            
            imgsArrangeArr[i]={
                pos : {
                    top : getRangeRandom(hPosRangeY[0],hPosRangeY[1]),
                    left : getRangeRandom(hPosRangeLORX[0],hPosRangeLORX[1])
                },
                rotate : get30DegRandom(),
                isCenter : false
            };
        }
        
        if(imgsArrangeTopArr && imgsArrangeTopArr[0]){
            imgsArrangeArr.splice(topImgSpliceIndex, 0,imgsArrangeTopArr[0]);
        }

        imgsArrangeArr.splice(centerIndex, 0 ,imgsArrangeCenterArr[0]);
        
        this.setState({
            imgsArrangeArr :imgsArrangeArr
        });
    } 

    /*
     * rearrange index
     * @param index index
     * @return {Function}
     */
    center(index){
        return () =>this.rearrange(index);
    }

    //
    componentDidMount(){
        //
        let stageDOM = this.stage,
            stageW = stageDOM.scrollWidth,
            stageH = stageDOM.scrollHeight,
            halfStageW = Math.ceil(stageW / 2),
            halfStageH = Math.ceil(stageH / 2);
        //imgFigure
        let imgFigureDOM = ReactDOM.findDOMNode(this.imgFigure0).firstChild,
            imgW = imgFigureDOM.scrollWidth,
            imgH = imgFigureDOM.scrollHeight,
            halfImgW = Math.ceil(imgW / 2),
            halfImgH = Math.ceil(imgH / 2);
        //
        this.Constant.centerPos = {
            left:halfStageW -halfImgW,
            top :halfStageH -halfImgH
        };

        //
        this.Constant.hPosRange.leftSecX[0] = -halfImgW;
        this.Constant.hPosRange.leftSecX[1] = halfStageW - halfImgW * 3;
        this.Constant.hPosRange.rightSecX[0] = halfStageW + halfImgW;
        this.Constant.hPosRange.rightSecX[1] = stageW - halfImgW;
        this.Constant.hPosRange.y[0] = -halfImgH;
        this.Constant.hPosRange.y[1] = stageH - halfImgH;
        
        //
        this.Constant.vPosRange.topY[0] = -halfImgH;
        this.Constant.vPosRange.topY[1] = halfStageH - halfImgH * 3;
        this.Constant.vPosRange.x[0] = halfStageW - imgW;
        this.Constant.vPosRange.x[1] = halfStageW;

        this.rearrange(0);
    }

    render(){
        let controllerUnits =[],
            imgFigures =[];

        imagesDate.forEach((value,index) => {
            if(!this.state.imgsArrangeArr[index]){
                this.state.imgsArrangeArr[index] = {
                    pos :{
                        left : 0,
                        top : 0
                    },
                    rotate : 0,
                    isInverse : false,
                    isCenter : false,
                };
            }
            imgFigures.push(<ImgFigure date={value} key={'imgFigure:'+index} ref={input => this['imgFigure' + index] = input} 
                arrange={this.state.imgsArrangeArr[index]} inverse={this.inverse(index)} center={this.center(index)}/>);
            controllerUnits.push(<ControllerUnit key={'controller:'+index} arrange={this.state.imgsArrangeArr[index]} inverse={this.inverse(index)} center={this.center(index)}/>);
        });    
        return (
            <div>
                <section className="stage" ref={input => this.stage = input}>
                    <section className="img-sec">{imgFigures}</section>
                    <nav className="controller-nav">{controllerUnits}</nav>
                </section>
            </div>
        );
    }
}


ReactDOM.render(
    <ReactGallery/>, document.getElementById('root'));


